@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  margin: 0;
  min-height: 100%;
  background-color: #e7e5e5;
  font-family: "PT Sans", sans-serif;
}

.site-wrapper {
  max-width: 1200px;
  min-width: 320px;
  margin: 0 auto;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.main-header {
  padding: 17px 18px;
  min-height: 56px;
  display: flex;
  background-color: #ffffff;
  align-items: center;
  position: relative;
}

.logo {
  height: 96px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.main-nav {
  margin-left: auto;
}
@media (min-width: 768px) {
  .main-nav {
    margin: 0;
    width: 100%;
  }
}

.main-nav__list {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: block;
    position: static;
  }
}

.main-nav__control-input {
  margin: 0;
}

.main-nav__control {
  display: flex;
  align-items: center;
  width: 22px;
  height: 22px;
}
@media (min-width: 768px) {
  .main-nav__control {
    display: none;
  }
}

.main-nav__burger {
  display: block;
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
  position: relative;
  z-index: 2;
}
.main-nav__burger::before, .main-nav__burger::after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
  position: absolute;
  left: 0;
}
.main-nav__burger::before {
  top: -9px;
}
.main-nav__burger::after {
  bottom: -9px;
}
.main-nav__burger:hover {
  opacity: 0.6;
}
.main-nav__burger:active {
  opacity: 0.3;
}

.main-nav__list {
  transition: all 0.5s ease-in-out;
}

#burger:checked + label + ul {
  display: block;
}

#burger:checked + label {
  position: fixed;
  top: 17px;
  right: 20px;
  z-index: 3;
}

#burger:checked + label span {
  background-color: #ffffff;
  transform: rotate(45deg);
}

#burger:checked + label span::before {
  background-color: #ffffff;
  top: 0;
  left: 0;
  transform: rotate(90deg);
}

#burger:checked + label span::after {
  display: none;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #81b3d2;
  text-align: center;
}
@media (min-width: 768px) {
  .site-list {
    background: none;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}

.site-list__item {
  padding: 17px 0 12px;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .site-list__item {
    padding: 0;
    border: none;
  }
  .site-list__item:nth-child(3) {
    grid-column: 4/5;
  }
}

.site-list__item:last-child {
  border-bottom: none;
  box-shadow: inset 0px -10px 15px -10px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .site-list__item:last-child {
    box-shadow: none;
  }
}

.site-list__item:first {
  border-top: none;
}

.site-list__link {
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
}
.site-list__link:hover {
  color: #766357;
}
@media (min-width: 768px) {
  .site-list__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
}

.page-main {
  background-color: #ffffff;
}

.hero {
  background: url("../img/mobile/hero-triangle.svg") no-repeat center bottom 0, url("../img/mobile/back-mobile.jpg") no-repeat center;
  background-size: 100% auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 90px;
  padding-bottom: 85px;
}
@media (min-width: 768px) {
  .hero {
    background: url("../img/tablet/back-tablet.jpg") no-repeat center;
    background-size: 100% auto;
    padding-bottom: 0;
  }
}
@media (min-width: 1200px) {
  .hero {
    background: url("../img/desktop/back-desktop.jpg") no-repeat center;
    background-size: 100% auto;
    padding-bottom: 0;
  }
}

.hero__greetings {
  display: block;
}
@media (min-width: 768px) {
  .hero__greetings {
    width: 461px;
    height: 350px;
  }
}

.hero__decor {
  display: none;
}
@media (min-width: 768px) {
  .hero__decor {
    display: block;
    justify-self: flex-end;
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .hero__decor {
    margin-bottom: 11px;
  }
}

.intro {
  text-align: center;
  padding: 32px 18px;
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .intro {
    padding-top: 47px;
    padding-bottom: 56px;
  }
}

.intro__title {
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 35px;
  text-transform: uppercase;
  width: 272px;
}
@media (min-width: 768px) {
  .intro__title {
    font-size: 21px;
    width: 520px;
  }
}

.intro__subtitle {
  font-size: 14px;
  line-height: 21px;
  font-weight: normal;
  color: #333333;
  margin: 0;
  text-transform: uppercase;
}

.advantager {
  position: relative;
  top: -43px;
  z-index: 1;
  margin-bottom: -43px;
}
@media (min-width: 768px) {
  .advantager {
    position: static;
    margin-bottom: 0;
  }
  .advantager .advantager__decor {
    display: none;
  }
}

.advantager__decor {
  width: 100%;
}

.advantager__card {
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .advantager__card {
    flex-direction: row;
    justify-content: space-around;
  }
}

.advantager__card_extend {
  background-color: #81b3d2;
}

@media (min-width: 768px) {
  .advantager__image-box {
    flex-grow: 1;
    max-height: 256px;
    overflow: hidden;
  }
}

.advantager__card_modify {
  background-color: #ffffff;
  padding: 23px 20px 30px 18px;
}
@media (min-width: 768px) {
  .advantager__card_modify {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
.advantager__card_modify .advantager__card-content {
  padding: 0;
  display: grid;
  grid-template-columns: 48px 1fr;
  justify-items: left;
  text-align: left;
  margin-bottom: 22px;
}
.advantager__card_modify .advantager__card-content .advantager__card-title {
  margin-top: 8px;
}
@media (min-width: 768px) {
  .advantager__card_modify .advantager__card-content .advantager__card-title {
    margin-top: 26px;
    margin-bottom: 26px;
  }
}
.advantager__card_modify .advantager__card-content .advantager__card-text {
  grid-column: 2/3;
  margin-top: 0;
}
@media (min-width: 768px) {
  .advantager__card_modify .advantager__card-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-bottom: 0;
    width: 193px;
  }
}

.advantager__card_modify-grey {
  background-color: #eeeeee;
  padding: 23px 20px 30px 18px;
}
.advantager__card_modify-grey .advantager__card-content {
  padding: 0;
  margin-bottom: 22px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.advantager__card_modify-grey .advantager__card-content .advantager__card-title {
  margin-top: 0;
}
.advantager__card_modify-grey .advantager__card-content .advantager__card-text {
  margin-top: 29px;
}
@media (min-width: 768px) {
  .advantager__card_modify-grey .advantager__card-content {
    margin-bottom: 0;
    padding-top: 42px;
    padding-bottom: 63px;
    padding-left: 33px;
    padding-right: 34px;
  }
  .advantager__card_modify-grey .advantager__card-title {
    max-width: 159px;
  }
}

.advantager__card-content {
  padding: 54px 22px 40px 16px;
}
@media (min-width: 768px) {
  .advantager__card-content {
    padding-top: 44px;
    flex-grow: 0;
    max-width: 256px;
    max-height: 256px;
  }
}
@media (min-width: 1200px) {
  .advantager__card-content {
    min-width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.advantager__card-title {
  margin-top: 0;
  margin-bottom: 11px;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .advantager__card-title {
    font-size: 21px;
  }
}

.advantager__card-title_black {
  color: #000000;
}

.advantager__card-number,
.advantager__card-text {
  font-size: 14px;
  line-height: 21px;
  font-weight: normal;
  color: #ffffff;
  text-transform: uppercase;
}

.advantager__card-text {
  margin-top: 17px;
  margin-bottom: 0;
}
@media (min-width: 1200px) {
  .advantager__card-text {
    width: 290px;
  }
}

.advantager__card-text_black {
  color: #000000;
}

.advantager__card-number_black {
  color: #000000;
}

.advantager__card-img {
  display: block;
  width: 100%;
  max-height: 256px;
  object-fit: cover;
  object-position: center;
}